<template>
    <v-container
        class="section"
        fluid
    >
        <v-row
            justify="center"
            class="title"
        >
            <v-col sm="8">
                <AnimateWhenVisible name="fadeDown">
                    <span
                        key="span1"
                        class="section-title deepshadow title-family"
                    >Genres</span>
                    <span
                        key="span2"
                        class="section-content content-family"
                    >Top movies for each genre</span>
                </AnimateWhenVisible>
            </v-col>
        </v-row>
        <v-row
            justify="center"
            class="movie-category"
        >
            <AnimateWhenVisible name="fadeUp">
                <a
                    v-for="genre in getUserTaste"
                    :key="'genreMoviesLabel' + genre"
                    class="effect-4 content-family"
                    @click="selectGenre(genre)"
                >
                    {{ genre }}
                </a>
            </AnimateWhenVisible>
        </v-row>
        <v-row justify="center">
            <v-col
                cols="10"
                class="movie-slider"
            >
                <a
                    @click="movePrevPage()"
                    class="arrow left"
                />
                <div
                    v-for="(movie, index) in currentMovies"
                    :key="'personalized' + movie.id + index"
                    class="tile"
                >
                    <v-img
                        class="tile-img"
                        :src="movie.poster_path | imagePath"
                        @click="viewMovie(movie.id)"
                    />
                </div>
                <a
                    class="arrow right"
                    @click="moveNextPage()"
                />
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
import { createNamespacedHelpers } from 'vuex';

const { mapState } = createNamespacedHelpers('users');
const movieMapActions = createNamespacedHelpers('movies').mapActions;
const movieMapState = createNamespacedHelpers('movies').mapState;

export default {
    name: 'GenreMovies',
    filters: {
        imagePath(value) {
            return value === '' ? '/static/img/no_poster.png' : value;
        }
    },
    props: {
        setLoaded: {
            type: Function,
            default: null
        }
    },
    data() {
        return {
            selectedGenre: undefined,
            currentIndex: 0,
            currentPage: 0,
            showCount: 6,
            maxPage: 2,
            selectedIndex: 0,
            totalCnt: 20
        };
    },
    computed: {
        ...mapState(['user']),
        ...movieMapState(['genreMovies']),
        getUserTaste() {
            if (this.user) {
                return this.user.movie_taste || this.user.movie_taste !== '' ? this.user.movie_taste : ['Animation', 'Action'];
            }
            return ['Animation', 'Action'];
        },
        currentMovies() {
            if (this.getGenreMovies.length === 0) {
                return [];
            }
            const start = this.currentIndex;
            const end = this.currentIndex + this.showCount;
            let result = [];
            if (end >= this.getGenreMovies.length) {
                result = result.concat(this.getGenreMovies.slice(start));
                result = result.concat(this.getGenreMovies.slice(0, end - this.getGenreMovies.length));
            } else {
                result = result.concat(this.getGenreMovies.slice(start, end));
            }
            return result;
        },
        getGenreMovies() {
            if (!(this.selectedGenre in this.genreMovies)) {
                return [];
            }
            return this.genreMovies[this.selectedGenre];
        }
    },
    watch: {
        getUserTaste(val) {
            if (val) {
                this.$log.debug('GenreMovies.vue getUserTaste watch', val);
                this.setLoaded(false);
                this.getMoviesByGenres(val).then((preference) => {
                    this.$log.debug('GenreMovies.vue getMoviesByGenres response preference', preference);
                    this.selectGenre(preference);
                }).then(() => {
                    this.$log.debug('GenreMovies.vue getMoviesByGenres setLoaded');
                    this.setLoaded(true);
                });
            }
        }
    },
    mounted() {
        if (this.currentMovies.length === 0 && this.getUserTaste.length !== 0) {
            this.$log.debug(this.getUserTaste);
            this.setLoaded(false);
            this.getMoviesByGenres(this.getUserTaste).then((preference) => {
                this.selectGenre(preference);
            }).then((() => {
                this.setLoaded(true);
            }));
        }
        window.addEventListener('resize', () => {
            const windowWidth = window.innerWidth;
            let result = 2;
            if (windowWidth > 1600) {
                result = 6;
            } else if (windowWidth > 1400) {
                result = 5;
            } else if (windowWidth > 1200) {
                result = 4;
            } else if (windowWidth > 1000) {
                result = 3;
            }
            this.showCount = result;
        });
    },
    methods: {
        ...movieMapActions(['getMoviesByGenres', 'addMovieView']),
        selectGenre(genre) {
            this.selectedGenre = genre;
            this.currentIndex = 0;
            this.currentPage = 0;
            this.selectedIndex = 0;
        },
        moveNextPage() {
            this.currentIndex = this.currentIndex + this.showCount;
            if (this.getGenreMovies.length <= this.currentIndex) {
                this.currentIndex = this.currentIndex - this.getGenreMovies.length;
            }
        },
        movePrevPage() {
            this.currentIndex = this.currentIndex - this.showCount;
            if (this.currentIndex < 0) {
                this.currentIndex = this.getGenreMovies.length + this.currentIndex;
            }
        },
        viewMovie(id) {
            this.addMovieView(id);
            this.$router.push(`/movies/detail/${id}`);
        }
    }
};
</script>

